@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@200;300;400;500;600;700&display=swap');


//laptop
.active-card-container{
    height:100%;
    width:100%;
    display:flex;
    flex-direction:column;
    align-items:center;
    position:relative;
    @media only screen and (max-width : 959px){
        display:none;
    }   

    .active-num{
        color:#E85A4F;
        font-family:"Inconsolata";
    }

    .incidentRate-num{
        color:#d6ce15;
        font-family:"Inconsolata";
    }

    .card-header{
        border-top:1px solid #053366;
        border-right:1px solid #053366;
        border-left:1px solid #053366;
        text-align:center;
        border-bottom:1px solid #f5f5f5;
        width:100%;
        padding:clamp(0px, 2.5vh, 25px) 0;
        background-color:#244c83;

        .card-header-text{
            display:flex;
            flex-direction:column;
            align-items:center;
            font-size:clamp(10px, 1vw, 15px);
            @media only screen and (max-height : 700px){
                font-size:clamp(9px, 1vw, 13px);
            }  
            @media only screen and (max-height : 600px){
                font-size:clamp(8px, .9vw, 12px);
            } 
            @media only screen and (max-height : 550px){
                font-size:clamp(8px, .8vw, 11px);
            }  
            @media only screen and (max-height : 500px){
                font-size:clamp(7px, .7vw, 10px);
            }  
            @media only screen and (max-height : 450px){
                font-size:clamp(7px, .6vw, 9px);
            }
        }

        .card-header-num{
            font-size:clamp(24px, 2.4vw, 36px);
            margin:0;

            @media only screen and (max-height : 700px){
                font-size:clamp(21px, 2.3vw, 33px);
            }   
            @media only screen and (max-height : 600px){
                font-size:clamp(18px, 2.2vw, 30px);
            }  
            @media only screen and (max-height : 550px){
                font-size:clamp(15px, 2.1vw, 27px);
            }  
            @media only screen and (max-height : 500px){
                font-size:clamp(12px, 2.0vw, 24px);
            }  
            @media only screen and (max-height : 450px){
                font-size:clamp(10px, 1.9vw, 21px);
            }  
        }
    }

    .card-header-incident{
        padding:clamp(0px, 1.5vh, 20px) 0;
    }

    .province-card-note{
        width:100%;
        background-color:#244c83;
        border-right:1px solid #053366;
        border-left:1px solid #053366;
        text-align:center;
        padding:clamp(3px, .7vh, 10px) 0;
        display:flex;
        flex-direction:column;
        flex-wrap:wrap;
        font-size:clamp(8px, .9vw, 18px);
        @media only screen and (max-height : 700px){
            font-size:clamp(7px, .8vw, 16px);
        }  
        @media only screen and (max-height : 600px){
            font-size:clamp(7px, .7vw, 14px);
        } 
        @media only screen and (max-height : 550px){
            font-size:clamp(7px, .6vw, 12px);
        }  
        @media only screen and (max-height : 500px){
            font-size:clamp(6px, .5vw, 10px);
        }  
        @media only screen and (max-height : 450px){
            font-size:clamp(6px, .4vw, 8px);
        }
    }

//ul
    .ul{
        width:100%;
        overflow-y:auto;
        background-color:#244c83;
        border-bottom:1px solid #053366;
        border-right:1px solid #053366;
        border-left:1px solid #053366;
    
        }
    
        .province-list{
            padding:clamp(0px, 1vh, 15px) 0 clamp(0px, 1vh, 15px) 7px;
            border-bottom:1px solid #f5f5f5;
            display:flex;
            flex-direction:column;
    
            .province-list-cases{
                font-size:clamp(10px, 1vw, 16px);
                color:#f5f5f5;
                font-family:"Work Sans";
                @media only screen and (max-height : 700px){
                    font-size:clamp(10px, .9vw, 13px);
                }  
                @media only screen and (max-height : 600px){
                    font-size:clamp(8px, .8vw, 10px);
                }  
            }
    
            .province-list-place{
                font-size:clamp(12px, 1vw, 16px);
                @media only screen and (max-height : 700px){
                    font-size:clamp(10px, .9vw, 13px);
                }  
                @media only screen and (max-height : 600px){
                    font-size:clamp(8px, .8vw, 10px);
                }  
            }
        }
        .list-num{
            margin-right:.2em;
            display:flex;
            align-items:center;
            flex-wrap:wrap;
            font-size:clamp(15px, 1.2vw, 18px);
            @media only screen and (max-height : 700px){
                font-size:clamp(13px, 1.1vw, 15px);
            }  
            @media only screen and (max-height : 600px){
                font-size:clamp(11px, 1vw, 12px);
            }  
            .province-list-cases{
                margin-left:.2em;
            }
        }
}

