.country-picker-container{
    width:100%;
    height:100%;
    display:flex;
    justify-content:center;
    z-index:1;
    @media only screen and (max-width: 959px) {
        display: none;
    }

    #form{
        z-index:1;
        height:100%;
        width:22%;

        #select{
            background-color:#474747;
            color:#f5f5f5;
            padding:0 3px 3px 3px;
            font-size:clamp(10px, 1.1vw, 18px);
            @media only screen and (max-height : 700px){
                font-size:clamp(10px, 1.1vw, 16px);
            }  
            @media only screen and (max-height : 600px){
                font-size:clamp(9px, 1.1vw, 14px);
            } 
            @media only screen and (max-height : 550px){
                font-size:clamp(8px, 1.1vw, 12px);
            } 
            @media only screen and (max-height : 500px){
                font-size:clamp(8px, 1.1vw, 11px);
            }  
            @media only screen and (max-height : 450px){
                font-size:clamp(7pxpx, 1.1vw, 10px);
            }   
        }

        #label{
            margin:0;
            color:#f5f5f5;
            font-size:clamp(10px, 1vw, 13px);

            @media only screen and (max-height : 550px){
                font-size:clamp(10px, .8vw, 12px);
            } 
            @media only screen and (max-height : 500px){
                font-size:clamp(10px, .7vw, 11px);
            }  
            @media only screen and (max-height : 450px){
                font-size:clamp(9px, .7vw, 10px);
            }  
        }
    }
}   


//expand
.country-picker-container-expand{
    width:100%;
    height:100%;
    display:flex;
    justify-content:center;
    z-index:1;
    
    #form{
        z-index:1;
        height:100%;

        #select{
            background-color:#474747;
            color:#f5f5f5;
            padding:0 24px 3px 0;
            font-size:clamp(15px, 1.2vw, 18px);
            @media only screen and (max-height : 700px){
                font-size:clamp(14px, 1.2vw, 16px);
            }  
            @media only screen and (max-height : 600px){
                font-size:clamp(13px, 1.2vw, 14px);
            } 
            @media only screen and (max-height : 550px){
                font-size:clamp(12px, 1.2vw, 12px);
            } 
            @media only screen and (max-height : 500px){
                font-size:clamp(11px, 1.2vw, 11px);
            }  
            @media only screen and (max-height : 450px){
                font-size:clamp(11px, 1.2vw, 10px);
            }   
        }

        #label{
            color:#f5f5f5;
            font-size:clamp(12px, 1.1vw, 15px);

            @media only screen and (max-height : 550px){
                font-size:clamp(12px, .8vw, 14px);
            } 
            @media only screen and (max-height : 500px){
                font-size:clamp(11px, .7vw, 13px);
            }  
            @media only screen and (max-height : 450px){
                font-size:clamp(11px, .7vw, 12px);
            }  
        }
    }   
}

